@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400;500;700&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  overflow-x: hidden;
}

body {
  font-family: "Noto Sans JP", sans-serif;
}

.l-content h1 {
  font-size: var(--font-xl);
}
.l-content h2 {
  font-size: var(--font-xl);
}
.l-content h3 {
  font-size: var(--font-lg);
}
.l-content h4 {
  font-size: var(--font-md);
}
.l-content h5 {
  font-size: var(--font-sm);
}
.l-content p {
  font-size: var(--font-sm);
}

ol,
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

img {
  vertical-align: bottom;
}

a {
  display: block;
}

/* Breakpoints(Arkhe) */
/* 見出しデザイン */
/* トップページ */
.l-main__body.p-front > .c-postContent {
  margin-top: 0;
}

[data-sidebar=on] .l-content__body {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  flex-direction: column-reverse;
  row-gap: 40px;
}
@media (min-width: 1000px) {
  [data-sidebar=on] .l-content__body {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    flex-direction: row-reverse;
    row-gap: 0;
  }
}

[data-sidebar=on] .l-sidebar {
  display: none;
}
@media (min-width: 1000px) {
  [data-sidebar=on] .l-sidebar {
    display: block;
    padding-bottom: 5rem;
  }
}

.p-hero__particles + .l-content__body {
  margin-top: 0;
}
@media (min-width: 600px) {
  .p-hero__particles + .l-content__body {
    margin-top: -120px;
  }
}
@media (min-width: 1000px) {
  .p-hero__particles + .l-content__body {
    margin-top: -120px;
  }
}
@media (min-width: 600px) {
  .p-hero__particles + .l-content__body .l-main {
    padding-top: 40px;
    padding-right: 40px;
    padding-left: 40px;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    background: #ffffff;
  }
}
.p-hero__particles + .l-content__body .p-entry__head .c-postMetas {
  color: var(--color-spindle);
}

.p-hero__blendParticles + .l-content > .l-content__body {
  margin-top: 0;
}
@media (min-width: 600px) {
  .p-hero__blendParticles + .l-content > .l-content__body {
    margin-top: -120px;
    padding-top: 80px;
    padding-right: 40px;
    padding-left: 40px;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    background: var(--color-primary);
  }
}
@media (min-width: 1000px) {
  .p-hero__blendParticles + .l-content > .l-content__body {
    margin-top: -120px;
  }
}
.p-hero__blendParticles + .l-content > .l-content__body .p-entry__head .c-postMetas {
  color: var(--color-spindle);
}

.p-postList.-type-simple .p-postList__link:hover {
  background-color: rgba(247, 247, 247, 0.4);
}

.c-fixBtn {
  background: var(--color-linear-fly-high-light);
  border-radius: 100px;
}

.wp-block-image:not(.is-style-rounded) > a,
.wp-block-image:not(.is-style-rounded) img {
  border-radius: inherit;
  max-width: 100%;
  width: 100%;
}

.p-postList.-type-list .p-postList__item {
  padding: 0.875rem 0;
  border-bottom: 1px solid var(--ark-color--border);
}
.p-postList.-type-list .p-postList__item:first-child {
  border-top: 1px solid var(--ark-color--border);
}

.ark-block-heading .ark-block-heading__line:before {
  background-color: currentColor;
  border-radius: 2px;
  content: "";
  display: block;
  height: 2px;
  width: 2em;
}

/* メインビジュアル #top_title_area */
#top_title_area.p-topArea {
  height: 50vh;
  text-align: left;
}
#top_title_area.p-topArea .p-topArea__title {
  text-align: left;
}

.p-topArea__excerpt {
  text-align: left;
}

/**
* トップページ
**/
.p-piece .p-postList.-type-card .p-postList__item {
  margin-bottom: 1rem;
}

.p-index__news .c-more__arrow.u-uppercase {
  text-align: right;
}

/* 箇条書きリスト(チェックリスト) */
.p-post-definition__desc [class*=is-style-ark-list-icon] > li:before {
  background-color: var(--color-half-baked);
}

.l-sidebar > #ez-toc-container {
  width: 100%;
  border: 0 none;
  box-shadow: none;
}

:root,
:before,
:after {
  /*arkhe-width */
  --ark-width--container--xl: 1200px;
  --ark-width--container--md: 1000px;
  --ark-width--article--md: 1000px;
  /*arkhe-margin */
  --space-unit: 1rem;
  --space-xxs: calc(0.25 * var(--space-unit));
  --space-xs: calc(0.5 * var(--space-unit));
  --space-sm: calc(0.75 * var(--space-unit));
  --space-md: calc(1.5 * var(--space-unit));
  --space-lg: calc(2 * var(--space-unit));
  --space-xl: calc(2.5 * var(--space-unit));
  --space-xxl: calc(4 * var(--space-unit));
  --space-xxxl: calc(5 * var(--space-unit));
  --width-unit: 1rem;
  --width-xxxs: calc(0.125 * var(--width-unit));
  --arc-circle--md: 160px;
  --arc-circle--lg: 200px;
  --pseudo-arc-line: 40px;
  /* font-size */
  --font-xxs: clamp(0.625rem, 0.5rem + 0.3333vw, 0.75rem);
  --font-xs: clamp(0.75rem, 0.625rem + 0.3333vw, 0.875rem);
  --font-sm: clamp(0.875rem, 0.75rem + 0.3333vw, 1rem);
  --font-md: clamp(1rem, 0.75rem + 0.6667vw, 1.25rem);
  --font-lg: clamp(1.25rem, 1rem + 0.6667vw, 1.5rem);
  --font-xl: clamp(1.5rem, 1.4rem + 0.5vw, 2rem);
  --font-xxl: clamp(2rem, 1.5rem + 1.3333vw, 2.5rem);
  --font-xxxl: clamp(2.5rem, 1rem + 4vw, 4rem);
  --font-hero: clamp(1.75rem, 1rem + 2vw, 2.5rem);
  --font-hero--lg: clamp(2.5rem, 0.5rem + 5.3333vw, 4.5rem);
  --font-hero--xl: clamp(4rem, 2rem + 5.3333vw, 6rem);
  /*  Global Font */
  --global-font-xl: clamp(
    1.875rem,
    1.2083rem + 1.3889vw,
    2.25rem
  );
  --global-font-lg: clamp(
    1.75rem,
    1.9722rem + -0.463vw,
    1.625rem
  );
  --global-font-regular: clamp(
    0.875rem,
    0.6528rem + 0.463vw,
    1rem
  );
  --color-primary: #ffffff;
  --color-secondary: #333333;
  --color-success: #062d6a;
  --color-white-smoke: #f3f3f3;
  --color-light-gray: #cccccc;
  --color-taupe-gray: #888888;
  --color-granite-gray: #5f5f5f;
  --color-lime-green: #c8e4cc;
  --color-summer-sky: #48c6ef;
  --color-pattern-blue: #dcf0fa;
  --color-bahama-blue: #1e5285;
  --color-lapis-lazuli: #26619c;
  --color-sample-blue: #2b6ba8;
  --color-picton: #5ea0e0;
  --color-spindle: #b6bdc7;
  --color-smalt: #0f238c;
  --color-viking: #41afcd;
  --color-golden-yellow: #ffe600;
  --color-tangerine-yellow: #ffcd00;
  --color-tarragon-tease: #b4a983;
  --color-nordic: #093637;
  --color-half-baked: #5b8f8f;
  --color-weldon-blue: #80999c;
  --color-cinereous: #9c8380;
  --color-antiflash: #f2f5f5;
  --color-gainsboro: #dbdbde;
  --color-cadet-gray: #8aa5a8;
  --color-text-primary: #333333;
  --color-text-vulcan: #3c3e42;
  --color-text-eclipse: #3c3c3c;
  --color-primary-translucent: rgba(255 255 255 / 40%);
  --color-charcoal-translucent: rgba(68 68 68 /50%);
  --color-spindle-translucent: rgba(182, 189, 199, 40%);
  --color-moonlit-asteroid: linear-gradient(
    45deg,
    rgba(15, 32, 39, 0.86),
    rgba(44, 83, 100, 0.86) 100%,
    rgba(32, 58, 67, 0.86) 50%
  );
  --color-linear-weldon-blue: linear-gradient(
    45deg,
    rgba(128, 153, 156, 0.86) 0%,
    rgba(128, 156, 145, 0.86) 100%
  );
  --color-linear-orca: linear-gradient(
    to left,
    rgba(68, 160, 141, 0.72),
    rgba(9, 54, 55, 0.72)
  );
  --color-linear-primary: linear-gradient(
    180deg,
    #f1f0eb 0%,
    #f1f0eb 77.08%,
    #ffffff 100%
  );
  --color-linear-premium-white: linear-gradient(
    120deg,
    #d5d4d0 0%,
    #d5d4d0 1%,
    #eeeeec 31%,
    #efeeec 75%,
    #e9e9e7 100%
  );
  --color-linear-lime-green: linear-gradient(120deg, #c8e4cc 0%, #a5d4ad 100%);
  --color-linear-lemon-green: linear-gradient(120deg, #f8e89a 0%, #c8e4cc 100%);
  --color-linear-morpheus-den: linear-gradient(
    to right,
    #30cfd0 0%,
    #330867 100%
  );
  --color-linear-cloudy-knoxville: linear-gradient(
    to top,
    #fdfbfb 0%,
    #ebedee 100%
  );
  --color-linear-fly-high: linear-gradient(to top, #48c6ef 0%, #6f86d6 100%);
  --color-linear-fly-high-light: linear-gradient(
    to top,
    #a6e3f7 0%,
    #bec9ed 100%
  );
  --color-linear-night-sky: linear-gradient(
    to top,
    #1e3c72 0%,
    #1e3c72 1%,
    #2a5298 100%
  );
  --color-linear-royal-golden: linear-gradient(0deg, #ffe384 0%, #5570ce 100%);
}

.ark-block-section:where(.alignfull):not([data-inner=full]) > .ark-block-section__body {
  margin: 0 auto;
  max-width: calc(var(--ark-width--article--md, 1000px) + var(--ark-padding--container, 0px) * 2);
  padding-left: var(--ark-padding--container, 0);
  padding-right: var(--ark-padding--container, 0);
}
.ark-block-section:where(.alignfull):not([data-inner=full]) > .ark-block-section__body .ark-block-section:where(.alignfull):not([data-inner=full]) > .ark-block-section__body {
  margin: 0 auto;
  max-width: 100%;
  padding-left: 0;
  padding-right: 0;
}
@media (min-width: 1000px) {
  .ark-block-section:where(.alignfull):not([data-inner=full]) > .ark-block-section__body .ark-block-section:where(.alignfull):not([data-inner=full]) > .ark-block-section__body {
    max-width: calc(var(--ark-width--article--md, 1000px) + var(--ark-padding--container, 0px) * 2);
    padding-left: var(--ark-padding--container, 0);
    padding-right: var(--ark-padding--container, 0);
  }
}

.c-pageTitle__main {
  text-align: left;
  letter-spacing: 0.1em;
}
.c-pageTitle__sub--custom {
  font-size: 0.9rem;
  font-weight: 400;
}

.c-more__arrow.u-uppercase {
  display: block;
  margin-top: 0;
}
.c-more__arrow.u-uppercase a {
  position: relative;
  display: inline-block;
  text-decoration: none;
  color: var(--color-moonlit-asteroid);
  font-size: var(--font-md);
}
.c-more__arrow.u-uppercase a .c-more__arrow--text {
  position: relative;
  font-size: var(--font-sm);
  padding: 0 0 6px;
  display: inline-block;
  text-align: left;
  width: 160px;
}
.c-more__arrow.u-uppercase a .c-more__arrow--text::before {
  content: "";
  display: block;
  position: absolute;
  bottom: -4px;
  right: 8px;
  left: auto;
  transform: translate(-50%, -50%);
  width: 24px;
  height: 24px;
  border-radius: 50%;
  z-index: -1;
  transform-origin: left center;
  transform: translateX(0) scale(1);
  background: var(--color-moonlit-asteroid);
  background: var(--color-linear-weldon-blue);
  transition: all 1.2s;
}
.c-more__arrow.u-uppercase a .c-more__arrow--text::after {
  width: 160px;
  height: 8px;
  content: "";
  display: block;
  vertical-align: middle;
  background: repeat-x left center/contain;
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22300%22%20height%3D%228%22%20style%3D%22enable-background%3Anew%200%200%20300%208%22%20xml%3Aspace%3D%22preserve%22%3E%3Cpath%20style%3D%22fill%3Anone%3Bstroke%3A%23000%3Bstroke-width%3A2%3Bstroke-linecap%3Around%3Bstroke-linejoin%3Around%3Bstroke-miterlimit%3A10%22%20d%3D%22M36.509%207h262l-8.614-6%22%2F%3E%3C%2Fsvg%3E");
  transition: all 0.3s cubic-bezier(0.1, 0.15, 0.45, 1);
  position: absolute;
  bottom: 4px;
  right: 24px;
}
.c-more__arrow.u-uppercase a:hover .c-more__arrow--text::before {
  transform: translateX(8px) scale(1.2);
  background: var(--color-moonlit-asteroid);
  background: var(--color-linear-weldon-blue);
}
.c-more__arrow.u-uppercase a:hover .c-more__arrow--text::after {
  animation: btn-arrow-animation 1.2s;
}
@keyframes btn-arrow-animation {
  0% {
    background-position: right center;
  }
  100% {
    background-position: 160px center;
  }
}
.c-more__arrow.u-uppercase a.-textright {
  text-align: right;
}

.c-more__flex {
  margin-top: 2.5rem;
}
.c-more__flex__container {
  text-align: center;
}
.c-more__flex .c-more__flex--link {
  text-decoration: none;
  color: var(--color-secondary);
}
.c-more__flex .c-more__flex--link .c-more__flex--item {
  position: relative;
  display: inline-flex;
  align-items: center;
}
.c-more__flex .c-more__flex--link .c-more__flex--item::after {
  content: "";
  display: block;
  width: 8rem;
  height: 0.625rem;
  background: url("data:image/svg+xml;charset=utf8,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%2296%22%20height%3D%2210%22%20style%3D%22enable-background%3Anew%200%200%2096%2010%22%20xml%3Aspace%3D%22preserve%22%3E%3Cpath%20style%3D%22fill%3Anone%3Bstroke%3A%23000%3Bstroke-width%3A2%3Bstroke-linecap%3Around%3Bstroke-linejoin%3Around%3Bstroke-miterlimit%3A10%22%20d%3D%22M17%209h78l-6.414-8%22%2F%3E%3C%2Fsvg%3E") no-repeat right top 50%/8rem 0.625rem;
  position: absolute;
  left: 86px;
  transform: translateX(0);
  transition: transform 1s;
}
.c-more__flex .c-more__flex--link .c-more__flex--item.-white {
  color: var(--color-primary);
}
.c-more__flex .c-more__flex--link .c-more__flex--item.-white::after {
  content: "";
  display: block;
  width: 8rem;
  height: 0.625rem;
  background: url("data:image/svg+xml;charset=utf8,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%20268%208%22%20style%3D%22enable-background%3Anew%200%200%20268%208%22%20xml%3Aspace%3D%22preserve%22%3E%3Cpath%20style%3D%22fill%3Anone%3Bstroke%3A%23fff%3Bstroke-width%3A2%3Bstroke-linecap%3Around%3Bstroke-linejoin%3Around%3Bstroke-miterlimit%3A10%22%20d%3D%22M33%207h234l-7.693-6%22%2F%3E%3C%2Fsvg%3E") no-repeat right top 50%/8rem 0.625rem;
  position: absolute;
  left: 86px;
  transform: translateX(0);
  transition: transform 1s;
}
.c-more__flex .c-more__flex--link:hover .c-more__flex--item::after {
  transform: translateX(8px);
}
.c-more__flex .c-more__flex--link:hover .c-more__flex--item.-white::after {
  transform: translateX(8px);
}

.c-more__circle {
  text-align: right;
}
.c-more__circle--link {
  text-decoration: none;
  color: var(--color-secondary);
  display: inline-flex;
  margin-left: -24px;
}
.c-more__circle--item {
  position: relative;
  font-weight: 500;
  font-size: var(--font-sm);
  margin-left: 24px;
}
.c-more__circle--item svg {
  fill: var(--color-primary);
}
.c-more__circle--item:has(svg)::before {
  position: absolute;
  display: block;
  content: "";
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: var(--color-linear-fly-high);
  z-index: -1;
}

.p-postList.-type-card a.p-postList__link .p-postList__thumb.c-postThumb {
  box-shadow: 0px -1px 4px 0px rgba(0, 0, 0, 0.12), 0px -2px 24px -10px rgba(0, 0, 0, 0.12), 0px -4px 32px -16px rgba(0, 0, 0, 0.08);
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  overflow: hidden;
  position: relative;
}
.p-postList.-type-card a.p-postList__link .p-postList__thumb.c-postThumb::before {
  z-index: 2;
}
.p-postList.-type-card a.p-postList__link .p-postList__body {
  padding-top: 1.25rem;
  box-shadow: 0px 1px 4px 0px rgba(0, 0, 0, 0.06), 0px 2px 24px -10px rgba(0, 0, 0, 0.06), 0px 4px 32px -16px rgba(0, 0, 0, 0.04);
  padding-left: 0.875rem;
  padding-right: 0.875rem;
  padding-bottom: 2.5rem;
  background-color: var(--color-primary);
  border-bottom-right-radius: 8px;
  border-bottom-left-radius: 8px;
  transition: all 0.4s;
  aspect-ratio: 11/6;
}
.p-postList.-type-card a.p-postList__link .p-postList__body h2.p-postList__title {
  font-size: var(--font-sm);
  text-align: left;
  color: #80999c;
  font-weight: 500;
}
.p-postList.-type-card a.p-postList__link:hover .p-postList__thumb.c-postThumb {
  box-shadow: 0px -4px 6px 0px rgba(0, 0, 0, 0.1), 0px -6px 30px -5px rgba(0, 0, 0, 0.08), 0px -6px 40px -10px rgba(0, 0, 0, 0.06);
}
.p-postList.-type-card a.p-postList__link:hover .p-postList__body {
  box-shadow: 0px 4px 6px 0px rgba(0, 0, 0, 0.1), 0px 6px 30px -5px rgba(0, 0, 0, 0.08), 0px 6px 40px -10px rgba(0, 0, 0, 0.06);
}

.admin-bar .p-drawer {
  height: 100vh;
  top: 0;
}

.p-fluid {
  width: 280px;
  height: 280px;
  background: var(--color-linear-premium-white);
  -webkit-animation: fluidaction 30s ease 0s infinite;
  animation: fluidaction 30s ease 0s infinite;
}

@keyframes fluidaction {
  0% {
    border-radius: 63% 37% 54% 46%/55% 48% 52% 45%;
  }
  14% {
    border-radius: 40% 60% 54% 46%/49% 60% 40% 51%;
  }
  28% {
    border-radius: 54% 46% 38% 62%/49% 70% 30% 51%;
  }
  42% {
    border-radius: 61% 39% 55% 45%/61% 38% 62% 39%;
  }
  56% {
    border-radius: 61% 39% 67% 33%/70% 50% 50% 30%;
  }
  70% {
    border-radius: 50% 50% 34% 66%/56% 68% 32% 44%;
  }
  84% {
    border-radius: 46% 54% 50% 50%/35% 61% 39% 65%;
  }
  100% {
    border-radius: 63% 37% 54% 46%/55% 48% 52% 45%;
  }
}
.c-fluid__left--top {
  position: absolute;
  top: -16px;
}

.c-fluid__left--bottom {
  position: absolute;
  top: -40px;
  left: -16px;
}

.c-fluid--right {
  position: absolute;
  top: -20px;
  right: -8px;
}

.c-pnNav {
  display: flex;
  flex-wrap: wrap;
}
@media (min-width: 600px) {
  .c-pnNav {
    column-gap: 20px;
  }
}
@media (min-width: 600px) {
  .c-pnNav__item {
    width: calc((100% - 20px) / 2);
  }
}
.c-pnNav__item--bottom {
  width: 100%;
}
.c-pnNav__item--bottom a {
  display: block;
  text-align: center;
}

.c-pnNav__image img {
  aspect-ratio: 16/9;
  object-fit: cover;
  vertical-align: bottom;
}

.p-pagination__columns {
  margin-top: calc(var(--space-lg) * 1);
  display: flex;
  margin-left: -32px;
}

.p-pagination__column {
  flex: 1 0 calc(50% - 32px);
  width: calc(50% - 32px);
  margin-left: 32px;
}

.p-pagination__container .p-pagination__content {
  position: relative;
  overflow: hidden;
}
.p-pagination__container .p-pagination__content::before {
  display: block;
  content: "";
  position: absolute;
  z-index: 2;
  bottom: 0;
  left: 0;
  width: 100%;
  aspect-ratio: 3.236/1;
  border-bottom-right-radius: 8px;
  border-bottom-left-radius: 8px;
