.leftCurtainbg {
  display: block;
  content: "";
  position: fixed;
  z-index: 999;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  transform: scaleX(0);
  background-color: #313131;
  animation-name: curtainAnime;
  animation-duration: 0.8s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards;
}

@keyframes curtainAnime {
  0% {
    transform-origin: left;
    transform: scaleX(0);
  }
  50% {
    transform-origin: left;
    transform: scaleX(1);
  }
  50.001% {
    transform-origin: right;
  }
  100% {
    transform-origin: right;
    transform: scaleX(0);
  }
}
.fadeout {
  animation: fadeOut 1s;
  animation-fill-mode: both;
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.l-wrapper {
  opacity: 0; /*はじめは透過0に*/
}

/*bodyにpageOnクラスがついたらcontainerが出現*/
body.pageOn .l-wrapper {
  animation-name: PageAnimeOn;
  animation-duration: 1s;
  animation-delay: 0.8s;
  animation-fill-mode: forwards;
  opacity: 0;
}

@keyframes PageAnimeOn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.p-loader {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-color: var(--color-moonlit-asteroid);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99999;
  opacity: 1;
  visibility: visible;
  transition-property: opacity, visibility;
  transition-duration: 2s;
}
.p-loader.loaded {
  opacity: 0;
  visibility: hidden;
}

/* ローディング[spinner]アニメーション(初回のみ) */
.p-spinner,
.p-spinner::after {
  border-radius: 50%;
  width: 4em;
  height: 4em;
}

.p-spinner {
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
}

.p-spinner::after {
  content: "";
  display: block;
  position: absolute;
  border-radius: 50%;
  border-top: 0.4em solid rgba(255, 255, 255, 0.2);
  border-right: 0.4em solid rgba(255, 255, 255, 0.2);
  border-bottom: 0.4em solid rgba(255, 255, 255, 0.2);
  border-left: 0.4em solid #545454;
  transform: translateZ(0);
  animation: loadAnime 1.1s infinite linear;
}

@keyframes loadAnime {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}