@use '../global' as *;

.p-fluid {
  width: 280px;
  height: 280px;
  // background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
  background: var(--color-linear-premium-white);
  -webkit-animation: fluidaction 30s ease 0s infinite;
  animation: fluidaction 30s ease 0s infinite;
}

@keyframes fluidaction {
  0% {
    border-radius: 63% 37% 54% 46%/55% 48% 52% 45%;
  }
  14% {
    border-radius: 40% 60% 54% 46%/49% 60% 40% 51%;
  }
  28% {
    border-radius: 54% 46% 38% 62%/49% 70% 30% 51%;
  }
  42% {
    border-radius: 61% 39% 55% 45%/61% 38% 62% 39%;
  }
  56% {
    border-radius: 61% 39% 67% 33%/70% 50% 50% 30%;
  }
  70% {
    border-radius: 50% 50% 34% 66%/56% 68% 32% 44%;
  }
  84% {
    border-radius: 46% 54% 50% 50%/35% 61% 39% 65%;
  }
  100% {
    border-radius: 63% 37% 54% 46%/55% 48% 52% 45%;
  }
}

.c-fluid__left--top {
  position: absolute;
  top: -16px;
}

.c-fluid__left--bottom {
  position: absolute;
  top: -40px;
  left: -16px;
}

.c-fluid--right {
  position: absolute;
  top: -20px;
  right: -8px;
}
