@use '../global' as *;
:root,
:before,
:after {
  /*arkhe-width */
  //ark-width-container
  --ark-width--container--xl: 1200px;
  --ark-width--container--md: 1000px;
  //ark-width-article
  --ark-width--article--md: 1000px;

  /*arkhe-margin */

  //space
  --space-unit: 1rem; //root16px換算
  --space-xxs: calc(0.25 * var(--space-unit)); //4px
  --space-xs: calc(0.5 * var(--space-unit)); //8px
  --space-sm: calc(0.75 * var(--space-unit)); //12px
  --space-md: calc(1.5 * var(--space-unit)); //24px
  --space-lg: calc(2 * var(--space-unit)); //32px
  --space-xl: calc(2.5 * var(--space-unit)); //40px
  --space-xxl: calc(4 * var(--space-unit)); //64px
  --space-xxxl: calc(5 * var(--space-unit)); //80px

  //width
  --width-unit: 1rem; //root 16px換算
  --width-xxxs: calc(0.125 * var(--width-unit)); //2px

  //pseudo 疑似要素
  --arc-circle--md: 160px;
  --arc-circle--lg: 200px;
  --pseudo-arc-line: 40px; //疑似要素 arc横のline

  /* font-size */
  //Min10px Max12px
  --font-xxs: clamp(0.625rem, 0.5rem + 0.3333vw, 0.75rem);

  //Min 12px Max 14px (案)font-xxs
  --font-xs: clamp(0.75rem, 0.625rem + 0.3333vw, 0.875rem);

  //Min 14px Max 16px (案)font-reg-sm
  --font-sm: clamp(0.875rem, 0.75rem + 0.3333vw, 1rem);

  //Min 16px Max 20px (案)font-reg-md
  --font-md: clamp(1rem, 0.75rem + 0.6667vw, 1.25rem);
  //-font-md: clamp()

  //Min 20px Max 24px
  --font-lg: clamp(1.25rem, 1rem + 0.6667vw, 1.5rem);

  //Min 24px Max 32px
  --font-xl: clamp(1.5rem, 1.4rem + 0.5vw, 2rem);

  //Min 32px Max 40px
  --font-xxl: clamp(2rem, 1.5rem + 1.3333vw, 2.5rem);

  --font-xxxl: clamp(2.5rem, 1rem + 4vw, 4rem); // Min 40px Max 64px

  //Min28px Max 40px
  --font-hero: clamp(1.75rem, 1rem + 2vw, 2.5rem);

  //Min 40px Max 72px
  --font-hero--lg: clamp(2.5rem, 0.5rem + 5.3333vw, 4.5rem);

  //Min 64px, Max 96px
  --font-hero--xl: clamp(4rem, 2rem + 5.3333vw, 6rem);

  /*  Global Font */
  //H2
  --global-font-xl: clamp(
    1.875rem,
    1.2083rem + 1.3889vw,
    2.25rem
  ); //Min30px, Max36px

  //H3(モバイルフォント>デスクトップフォント)
  --global-font-lg: clamp(
    1.75rem,
    1.9722rem + -0.463vw,
    1.625rem
  ); //Min28px, Max26px

  //Paragraph
  --global-font-regular: clamp(
    0.875rem,
    0.6528rem + 0.463vw,
    1rem
  ); //Min14px Max16px

  // Color
  --color-primary: #{$c-primary};
  --color-secondary: #{$c-secondary};
  --color-success: #{$c-success};
  --color-white-smoke: #f3f3f3;
  --color-light-gray: #cccccc;
  --color-taupe-gray: #888888;
  --color-granite-gray: #5f5f5f;

  //Colorful
  --color-lime-green: #c8e4cc;
  --color-summer-sky: #48c6ef;
  --color-pattern-blue: #dcf0fa;
  --color-bahama-blue: #1e5285;
  --color-lapis-lazuli: #26619c;
  --color-sample-blue: #2b6ba8;
  --color-picton: #5ea0e0;
  --color-spindle: #b6bdc7;
  --color-smalt: #0f238c;
  --color-viking: #41afcd;
  --color-golden-yellow: #ffe600;
  --color-tangerine-yellow: #ffcd00;
  --color-tarragon-tease: #b4a983;
  --color-nordic: #093637;
  --color-half-baked: #5b8f8f;
  --color-weldon-blue: #80999c;
  --color-cinereous: #9c8380;
  --color-antiflash: #f2f5f5;
  --color-gainsboro: #dbdbde;
  --color-cadet-gray: #8aa5a8;

  //Text Color
  --color-text-primary: #333333;
  --color-text-vulcan: #3c3e42;
  --color-text-eclipse: #3c3c3c;

  //Color Translucent
  --color-primary-translucent: rgba(255 255 255 / 40%);
  --color-charcoal-translucent: rgba(68 68 68 /50%);
  --color-spindle-translucent: rgba(182, 189, 199, 40%);

  --color-moonlit-asteroid: linear-gradient(
    45deg,
    rgba(15, 32, 39, 0.86),
    rgba(44, 83, 100, 0.86) 100%,
    rgba(32, 58, 67, 0.86) 50%
  );
  --color-linear-weldon-blue: linear-gradient(
    45deg,
    rgba(128, 153, 156, 0.86) 0%,
    rgba(128, 156, 145, 0.86) 100%
  );
  // Linear Gradient
  --color-linear-orca: linear-gradient(
    to left,
    rgba(68, 160, 141, 0.72),
    rgba(9, 54, 55, 0.72)
  );
  --color-linear-primary: linear-gradient(
    180deg,
    #f1f0eb 0%,
    #f1f0eb 77.08%,
    #ffffff 100%
  );

  --color-linear-premium-white: linear-gradient(
    120deg,
    #d5d4d0 0%,
    #d5d4d0 1%,
    #eeeeec 31%,
    #efeeec 75%,
    #e9e9e7 100%
  );
  --color-linear-lime-green: linear-gradient(120deg, #c8e4cc 0%, #a5d4ad 100%);
  --color-linear-lemon-green: linear-gradient(120deg, #f8e89a 0%, #c8e4cc 100%);
  --color-linear-morpheus-den: linear-gradient(
    to right,
    #30cfd0 0%,
    #330867 100%
  );
  --color-linear-cloudy-knoxville: linear-gradient(
    to top,
    #fdfbfb 0%,
    #ebedee 100%
  );
  --color-linear-fly-high: linear-gradient(to top, #48c6ef 0%, #6f86d6 100%);
  --color-linear-fly-high-light: linear-gradient(
    to top,
    #a6e3f7 0%,
    #bec9ed 100%
  );
  --color-linear-night-sky: linear-gradient(
    to top,
    #1e3c72 0%,
    #1e3c72 1%,
    #2a5298 100%
  );
  --color-linear-royal-golden: linear-gradient(0deg, #ffe384 0%, #5570ce 100%);
}
